/* ==========================================================================
   Components Crypto
   ========================================================================== */

.crypto__box {
  $cb: &;
  padding: 48px 24px;
  border-radius: 16px;
  text-align: center;
  transition: all 0.2s ease;
  box-shadow: 0 40px 40px 0 rgba(57, 23, 119, .03);
  &:hover {
    background-color: color(secondary);
    #{$cb}__title,
    #{$cb}__desc {
      color: color(white);
    }
  }
  @include medium {
    padding: 40px 16px;
  }
  &__img {
    margin-bottom: 48px;
    @include large {
      margin-bottom: 32px;
    }
    @include extra-small {
      margin-bottom: 16px;
    }
    &__el {
      width: 80px;
      display: block;
      margin: 0 auto;
    }
  }
  &__title {
    margin-bottom: 40px;
    @include medium {
      margin-bottom: 32px;
    }
    @include extra-small {
      margin-bottom: 16px;
      @include font-size(h4);
    }
    &__small {
      @include font-size(h6);
      color: color(grey, 2);
    }
  }
  &__desc {
    color: color(grey, 3);
    margin-bottom: 24px;
  }
  &__btn {
    min-width: 62px;
    transition: all .3s ease;
    margin-right: 12px;
    $bt: &;
    #{$bt} {
      &__txt {
        color: color(white);
        @include font-size(h6);
        margin-right: 24px;
      }
      &__icon {
        background-color: color(white);
        background-image: url(../img/icons/arrow-right-small.svg);
        background-size: 8px auto;
        margin-left: 8px;
      }
    }
  }
}